@import url(https://fonts.googleapis.com/css?family=Nunito:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:800&display=swap);
/* @import url("https://fonts.googleapis.com/css?family=Big+Shoulders+Display|Roboto+Slab&display=swap"); */

/* @import url("https://fonts.googleapis.com/css?family=Righteous&display=swap"); */

body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Menu {
  width: 100vw;
  height: 100vh;

  -webkit-animation-timing-function: ease-out;

          animation-timing-function: ease-out;
  -webkit-animation: fade-in 700ms 1;
          animation: fade-in 700ms 1;

  display: grid;
  align-items: stretch;
  align-content: stretch;
  justify-items: stretch;
  justify-content: stretch;

  grid-template-columns: 2fr 2fr 2fr 2fr 2fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas:
    "G-head label1 label1 label1 label1"
    "G-head O-head label2 label2 label2"
    "about O-head sel1 sel2 sel3";
  grid-gap: 12px;

  font-family: "Raleway", sans-serif;
}

.Menu-grid-elem {
  border-radius: 26px;
  align-items: center;
}

.Menu-GO {
  background-color: #e3c4a8;
  vertical-align: middle;
  color: #33313b;
  cursor: default;
  max-height: 66vh;
  overflow: hidden;
}

.Menu-logo-text {
  line-height: 20%;
  margin: 80% 0 0 0;
  font-size: 40em;
}

.Menu-board-type {
  position: relative;
  background-color: #e3c4a8;
  color: #33313b;
  bottom: 0%;
  font-size: 20em;
  text-align: right;

  border: none;
  outline: none;
  overflow: hidden;
  -webkit-transition: all 700ms ease-in-out;
  transition: all 700ms ease-in-out;
  z-index: 0;
}

.Menu-board-type:hover {
  background-color: #aca;
  z-index: 2;
  cursor: pointer;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  bottom: 5%;
  -webkit-transition: all 400ms ease-out;
  transition: all 400ms ease-out;
}

.Menu-label1 {
  grid-area: label1;

  cursor: default;
  align-items: center;
  color: #e3c4a8;
  margin-top: 2%;
  font-size: 14em;
  overflow: hidden;
}

.Menu-label2 {
  grid-area: label2;

  line-height: 79%;
  cursor: default;
  align-items: center;
  color: #e3c4a8;
  font-family: "Raleway", sans-serif;

  font-size: 24em;
  overflow: hidden;
}

.Menu-G {
  grid-area: G-head;
}

.Menu-O {
  grid-area: O-head;
}

.Menu-btn-1 {
  grid-area: sel1;
}

.Menu-btn-2 {
  grid-area: sel2;
}

.Menu-btn-3 {
  grid-area: sel3;
}

@media (max-width: 1700px) {
  .Menu-logo-text {
    font-size: 34em;
  }

  .Menu-board-type {
    font-size: 15em;
  }
  .Menu-label1 {
    font-size: 9em;
  }
  .Menu-label2 {
    font-size: 18em;
  }
}

@-webkit-keyframes fade-in {
  from {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.MenuMobile {
  margin: 0;
  overflow: hidden;
  /* position: relative; */

  -webkit-animation-timing-function: ease-out;

          animation-timing-function: ease-out;
  -webkit-animation: fade-in-mobile 700ms 1;
          animation: fade-in-mobile 700ms 1;

  height: 100vh;
  width: 100vw;

  border-radius: 40px;

  text-align: center;
  text-justify: center;
  color: #e3c4a8;
}

.MenuMobile-GO-header {
  font-size: 14em;
  margin: 23% auto 0;
}

.MenuMobile-board-types-box {
  margin: 10% auto 0;
  width: 80%;

  display: flex;
  flex-wrap: wrap;

  text-align: center;
  text-justify: center;
  justify-content: space-around;
  align-content: center;
}

.MenuMobile-board-type {
  margin: 30px 25px 0;
  width: 130px;
  height: 130px;
  position: relative;
  line-height: 0;

  -webkit-transition: all 400ms ease-in-out;

  transition: all 400ms ease-in-out;

  border-radius: 50%;
  border: none;
  outline: none;

  background-color: #e3c4a8;
  color: #111;
  font-size: 3em;
}

.MenuMobile-board-type:hover {
  box-shadow: 0px 0px 35px #aca;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media (max-width: 480px) {
  .MenuMobile-GO-header {
    font-size: 10em;
    margin: 25% auto 0;
  }

  .MenuMobile-board-type {
    width: 90px;
    height: 90px;
    font-size: 2em;
  }
}

.MenuMobile footer {
  color: #e3c4a8;
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
  /* margin: 0 auto 0; */
}

@-webkit-keyframes fade-in-mobile {
  from {
    -webkit-transform: scale(0.92);
            transform: scale(0.92);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes fade-in-mobile {
  from {
    -webkit-transform: scale(0.92);
            transform: scale(0.92);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.Board {
  border-radius: 10px;
  display: inline-block;
  margin: auto;
  padding: 1% 0 1%;

  /* height: 80%; */
  width: 90%;
  max-height: 80%;

  position: relative;

  align-self: center;
  justify-self: center;

  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;

  justify-content: center;
  align-content: center;
}

.Board-row {
  flex-grow: 1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

@media (min-width: 650px) and (max-width: 1300px) {
  .Board {
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
    margin: 15vw auto 10%;
  }
}
@media (min-width: 950px) and (max-width: 1300px) {
  .Board {
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
  }
}

@media (min-width: 650px) and (max-width: 949px) {
  .Board {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}

.Field {
  background-color: #555;
  opacity: 0.3;
  border-radius: 20%;
  margin: 0;

  -webkit-transition: all 400ms ease-out;

  transition: all 400ms ease-out;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  flex-grow: 1;
}

.Field:before {
  content: "";
  display: block;
  height: 0;
  padding-bottom: 100%;
}

.Field-taken {
  opacity: 0.9;
  position: relative;
  z-index: 1;
  border-radius: 100%;
  box-shadow: 0px 1px 8px #333;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}

.Field-possible:hover {
  opacity: 1;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  border-radius: 25%;
  background-color: #aca;
  cursor: pointer;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}

.Field-black {
  background-color: #111;
}

.Field-white {
  background-color: #e3c4a8;
}

.PassButton {
  margin: auto;
  vertical-align: bottom;
  line-height: 0;

  outline: none;
  border: none;

  width: 80%;
  padding-top: 80%;
  border-radius: 50%;

  position: relative;
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
  -webkit-transition: all 700ms ease-in-out;
  transition: all 700ms ease-in-out;

  overflow: hidden;
  box-shadow: 0px 2px 14px #111;
}

.PassButton-content {
  position: absolute;

  font-size: 5.5rem;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  padding-top: 50%;

  opacity: 0;
  -webkit-transition: inherit;
  transition: inherit;
}

.PassButton-winner {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.PassButton-looser {
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
}

.PassButton-finished .PassButton-content {
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation: text-fade-in 700ms 1;
          animation: text-fade-in 700ms 1;
  opacity: 0.8;
}

.PassButton-active .PassButton-content {
  opacity: 0.1;
}

.PassButton-active:hover .PassButton-content {
  opacity: 0.7;
}

.PassButton-active {
  -webkit-transition: all 700ms ease-in-out;
  transition: all 700ms ease-in-out;

  -webkit-transform: scale(1);

          transform: scale(1);
  box-shadow: 0px 0px 35px #aca;
}

.PassButton-active:hover {
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  box-shadow: 0px 0px 55px #e78267;
  opacity: 0.9;
}

.PassButton-black {
  color: #e3c4a8;
  grid-area: black-player;
  background-color: #111;
}

.PassButton-white {
  color: #111;
  grid-area: white-player;
  background-color: #e3c4a8;
}

@media (min-width: 650px) and (max-width: 1300px) {
  .PassButton {
    width: 40%;
    padding-top: 40%;
  }
  .PassButton-content {
    font-size: 3rem;
  }
}

@media (max-width: 650px) {
  .PassButton-content {
    font-size: 2rem;
  }
}

@-webkit-keyframes text-fade-in {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.7;
  }
}

@keyframes text-fade-in {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.7;
  }
}

.Game {
  width: 100vw;
  height: 100vh;

  -webkit-animation-timing-function: ease-out;

          animation-timing-function: ease-out;
  -webkit-animation: fade-in 700ms 1;
          animation: fade-in 700ms 1;
  -webkit-transition: all 700ms ease-in-out;
  transition: all 700ms ease-in-out;

  display: grid;
  align-items: center;
  justify-items: center;

  grid-template-rows: 100%;
  grid-template-areas: "black-player board white-player";
  grid-template-columns: 1fr 2fr 1fr;
}

.Game-black-won {
  background-color: #222;
  background-blend-mode: darken;
}

.Game-white-won {
  background-color: #d3b296;
  background-blend-mode: darken;
}

.Game-board {
  grid-area: board;
  height: 100%;
  width: 100%;
  padding-top: 15vh;

  -webkit-transition: all 700ms ease-in-out;

  transition: all 700ms ease-in-out;
  position: relative;
}

.Game-board-win {
  -webkit-transform: scale(0.93);
          transform: scale(0.93);
}

.Game-pass {
  width: 100%;
  height: 100%;
  display: flex;
}

.Game-pass-black {
  grid-area: black-player;
}

.Game-pass-white {
  grid-area: white-player;
}

.Game footer {
  color: #e3c4a8;
  position: fixed;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
  /* margin: 0 auto 0; */

  text-shadow: 2px 1px 5px rgba(51, 49, 59, 0.78);
}

@media (max-width: 1300px) {
  .Game {
    /* grid-template-areas: "black-player board white-player";
  grid-template-columns: 1fr 2fr 1fr; */

    grid-template-rows: 60% 34% 6%;
    grid-template-columns: 50% 50%;
    grid-template-areas:
      "board board"
      "black-player white-player"
      ". .";
  }
}

@-webkit-keyframes fade-in {
  from {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.Interface {
  background-color: #33313b;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  min-height: 100%;

  text-align: center;
}

